html, body {
    width: 100%;
    background-color: #dddddd;
    font-family: Avenir, Optima, Charter, Cochin, Georgia, serif;
}

body {
    overflow: hidden;
}

#invisible-div {
    background-color: #000000;
    height: 500vh;
}

#initiate-scroll {
    position: fixed;
    bottom: 3%;
    left: 49%;
    z-index: 100;
    font-size: 2.5em;
    cursor: pointer;

    animation: initiate-scroll 1s ease-in-out infinite both;
    transition: opacity 0.5s ease-out;
}
@keyframes initiate-scroll {
    0% {
        bottom: 3%;
    }
    50% {
        bottom: 5%;
    }
    100% {
        bottom: 3%;
    }
}

#progress-bar {
    position: fixed;
    top: 0;
    left: 0;
    height: 2px;
    width: 0%;
    background-color: #ffffff;
    z-index: 1000;

    animation: progress-bar 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--progress) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}
@keyframes progress-bar {
    from {
        width: 0%;
    }
    to {
        width: 100%;
    }
}

#animated-container {
    width: 100%;
    background-color: #000000;
    color: #ffffff;
    height: 100vh;
    position: sticky;
    position: -webkit-sticky;
    top: 0%;
    font-weight: 500;
    z-index: 100;
}

#dev-col {
    flex-grow: 1;
    position: relative;
    justify-content: flex-end;

    animation: dev-col 1s ease infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}
@keyframes dev-col {
    40% {
        left: 0;
        transform: scale(1, 1);
    }
    60% {
        left: 46vw;
        transform: scale(0.5, 0.5);
    }
    100% {
        left: 46vw;
        transform: scale(0.5, 0.5);
    }
}

#dev-span {
    font-size: 18vw;
    position: relative;
    white-space: nowrap;
}

#em-span {
    opacity: 0;

    animation: em-span 1s ease infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}
@keyframes em-span {
    55% {
        opacity: 0;
    }
    75% {
        opacity: 100;
    }
    100% {
        opacity: 100;
    }
}

#elop {
    font-size: 18vw;
    position: absolute;
    top: 0%;

    animation: elop 1s ease infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}
@keyframes elop {
    20% {
        top: -20%;
        opacity: 0;
    }
    100% {
        top: -20%;
        opacity: 0;
    }
}

#ise {
    font-size: 18vw;
    position: absolute;
    top: 20%;
    opacity: 0;

    animation: ise 1s ease infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}
@keyframes ise {
    10% {
        top: 20%;
        opacity: 0;
    }
    30% {
        top: 0;
        opacity: 100;
    }
    40% {
        top: 0;
        opacity: 100;
        transform: translate(0, 0) scale(1, 1);
    }
    60% {
        top: 0;
        opacity: 0;
        transform: translate(150%, 0) scale(0.5, 0.5);
    }
    100% {
        top: 0;
        opacity: 0;
        transform: translate(150%, 0) scale(0.5, 0.5);
    }
}

#header {
    height: 52px;
    width: 100%;
    font-size: 25px;
    background-color: #000000;
    position: sticky;
    position: -webkit-sticky;
    top: 0;
    z-index: 80;
    color: #000000;

    animation: header 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}
@keyframes header {
    92% {
        color: #000000;
    }
    93% {
        color: #ffffff;
    }
    100% {
        color: #ffffff;
    }
}

#navbar {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 80px;
    left: 75%;
    visibility: hidden;
    opacity: 0;

    animation: navbar 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}

@keyframes navbar {
    99% {
        visibility: hidden;
        opacity: 0;
    }
    100% {
        visibility: unset;
        opacity: 100;
    }
}

.navbar-item {
    border-left: 0px solid #dddddd;
    padding-left: 1rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: #999999;
    transition: border-left 0.5s, margin-left 0.5s;
}

a:not([href]) {
    color: #555555;
    text-decoration: none;
}

.navbar-item:hover {
    color: #fff;
    font-weight: 500;
}

.navbar-item.active {
    margin-left: -5px;
    text-decoration: none;
    color: #000000;
    font-weight: 500;
    border-left: 5px solid #000000;
}

h2 {
    font-size: 3rem;
}

p, ul {
    font-size: 1.5rem;
}

.card {
    border-radius: 1rem;
}

.card-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 1rem;
    margin-bottom: 0;
}

.card-title h5 {
    margin: 0;
}

.card-text {
    font-size: 1rem;
}

svg {
    width: 75%;
    height: 75%;
    display: block;
    margin: 1rem auto;
}

.fa-plus-circle {
    color: rebeccapurple;
    font-size: 2rem;
    transition: transform 0.5s;
    cursor: pointer;
}

.project-logo {
    max-height: 100%;
    max-width: 100%;
    margin: 1rem 0;
    border-radius: 1rem;
}

.certificate {
    max-height: 100%;
    max-width: 100%;
    margin: 0;
}

.accordion {
    border-radius: 1rem;
}

.card-header {
    background-color: #ffffff;
}

h5.accordion {
    font-size: 1rem;
    text-decoration: none;
    text-align: left;
    padding: 0;
}

.connect-icons-small {
    display: flex;
    justify-content: space-evenly;
    font-size: 4rem;
}

.connect-icons {
    display: flex;
    position: fixed;
    bottom: 5%;
    width: 25%;
}

.connect {
    font-size: 4rem;
    padding-left: 1rem;
    color: #999999;
    transition: font-size 0.5s;
}

#github:hover {
    color: #000000;
}

#linkedin:hover {
    color: #0c66c2;
}

#facebook:hover {
    color: #1877f2;
}

hr {
    margin: 3rem 0;
}

th, td {
    padding: 0.5rem 1rem;
    border-right: 1px solid #aaaaaa;
    white-space: nowrap;
}

#back-to-top {
    border-radius: 10rem;
    background-color: #000000;
}

#side-menu {
    display: flex;
    height: 100%;
    width: 80vw;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 50;
    visibility: hidden;
    opacity: 0;

    transition: right 0.5s ease;

    animation: side-menu 1s linear infinite;
    animation-play-state: paused;
    animation-delay: calc(var(--scroll) * -1s);
    animation-iteration-count: 1;
    animation-fill-mode: both;
}

@keyframes side-menu {
    99% {
        visibility: hidden;
        opacity: 0;
    }
    100% {
        visibility: visible;
        opacity: 97.5;
    }
}

#side-menu-toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    width: 5vw;
    height: 10vh;
    background-color: #000000;
    color: #ffffff;
    border-top-left-radius: 0.5rem;
    border-bottom-left-radius: 0.5rem;
}

#chevron-left {
    transition: transform 0.5s;
}

#menu {
    padding-top: 52px;
    background-color: #ffffff;
    width: 75vw;

    transition: box-shadow 0.5s;
}